export const Storage = {
    /**
     * 
     * @param {String} key storage
     * @param {String} word 
     */
    add: function (key = '', word = '') {
        let words_list;
        if (typeof word == 'string') {
            word = word.replace(/[{}\\`'":]/g, '');
        }
        if (key == '' || word === '' || isExisted(key, word)) {
            console.log(Error('localStorage '));
            return;
        }
        if (localStorage[key]) {
            words_list = JSON.parse(localStorage[key]);
        } else {
            words_list = [];
        }
        words_list.push(JSON.parse(`{"${words_list.length}":"${word}"}`));
        localStorage[key] = JSON.stringify(words_list);
    },
    /**
     * 
     * @param {String} key storage
     * @return {Array} 
     */
    get: function (key) {
        if (localStorage[key]) {
            return JSON.parse(localStorage[key]);
        } else {
            return [];
        }
    },
    /**
     * 
     * @param {key} key storage
     * @return {Boolean} 
     */
    remove: function (key) {
        if (localStorage[key]) {
            localStorage.removeItem(key);
            return true;
        } else {
            return false;
        }
    }
}

/**
 * 
 * @param {String} key storage
 * @param {String} value 
 * @return {Boolean} 
 */
function isExisted(key, value) {
    let array = Storage.get(key);
    let flag = false;
    if (array.length) {
        array.forEach((item, index) => {
            if (item[index] == value) {
                flag = true;
            }
        });
    }
    return flag;
}


